// Variables
$gray-300: #d7e1ec;
$gray-400: #b4c0ce;
$gray-500: #a0a1ac;
$gray-600: #6b6f77;
$gray-900: #212325;
$black: #000;
$teal: #6c5ba7;
$pink: #6c5ba7;
$primary: #6c5ba7;
$secondary: #6c5ba7;
$success: $teal;
// Body background gradient
$gradient-color-1: adjust-color( $gray-900, $hue: 6deg, $saturation: 0%, $lightness: 2%); // #27292C
// Spacing
$spacer: 1rem;
$spacers: ( 0: 0, 1: ($spacer * .625), 2: ($spacer * 1.25), 3: ($spacer * 1.875), 4: ($spacer * 2.5), 5: ($spacer * 3.125));
// Body
$body-bg: $gray-900;
$body-color: $gray-500;
// Links
$link-hover-color: adjust-color( $primary, $hue: 0deg, $saturation: -2%, $lightness: -8%); // #58B4C9
// Grid breakpoints
$grid-breakpoints: ( xxs: 0, xs: 480px, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1600px);
// Grid containers
$container-max-widths: ( sm: 540px, md: 720px, lg: 960px, xl: 1200px, xxl: 1650px);
// Components
$border-color: rgba( #1D1F23, .8);
$border-radius-sm: px-to-rem( 5px);
$border-radius: px-to-rem( 10px);
$border-radius-lg: px-to-rem( 15px);
$border-radius-xl: px-to-rem( 20px);
$box-shadow-1: -4px -4px 15px rgba(#C3C8CD, 0.04);
$box-shadow-sm: 0 .125rem .25rem rgba($black, .075);
$box-shadow: px-to-rem( 20px, 15px, 22px) rgba(#151618, 0.24),
$box-shadow-1;
$box-shadow-lg: px-to-rem( 30px, 25px, 22px) rgba(#151618, 0.24),
$box-shadow-1;
$component-active-color: $gray-300;
$transition-longer: .5s ease-in-out;
$transition-longest: .7s ease-in-out;
// Tables
$table-cell-padding: px-to-rem( 30px, 12px);
$table-cell-padding-sm: px-to-rem( 12px, 10px);
$table-color: $gray-300;
$table-hover-color: $table-color;
$table-hover-bg: transparent;
// Divider
$hr-border-color: rgba( #1D1F23, .8);
//$hr-border-color: rgba( #282A2E, .8 );
$hr-margin-y: 0;
// Fonts
$font-family-sans-serif: 'Josefin Sans',
sans-serif;
$font-size-base: px-to-rem( 15px);
$font-size-lg: px-to-rem( 17px);
$line-height-base: 1.74;
$line-height-lg: 1.65;
$h1-font-size: px-to-rem( 70px);
$h2-font-size: px-to-rem( 46px);
$h3-font-size: px-to-rem( 38px);
$h4-font-size: px-to-rem( 30px);
$h5-font-size: px-to-rem( 20px);
$h6-font-size: px-to-rem( 17px);
$h1-line-height: 1.12;
$h2-line-height: 1.31;
$h3-line-height: 1.14;
$h4-line-height: 1.14;
$h5-line-height: 1.7;
$h6-line-height: 1.7;
$headings-font-weight: 700;
$headings-color: $gray-300;
$mark-padding: .134em;
$mark-bg: $primary;
// Buttons
$btn-padding-y: px-to-rem( 11px);
$btn-padding-x: px-to-rem( 40px);
$btn-font-size: px-to-rem( 20px);
$btn-line-height: 1.7;
$btn-padding-y-sm: px-to-rem( 10px);
$btn-padding-x-sm: px-to-rem( 30px);
$btn-font-size-sm: px-to-rem( 17px);
$btn-line-height-sm: 1.53;
$btn-border-width: px-to-rem( 2px, 2px, 2px, 3px);
$btn-font-weight: 500;
$btn-box-shadow: 8px 8px 22px rgba(#151618, 0.38),
-4px -2px 16px rgba(#C3C8CD, 0.08);
$btn-focus-box-shadow: $btn-box-shadow,
inset -2px -2px 4px rgba(#363639, 0.16),
inset 2px 2px 4px rgba(#1E1E20, 0.18);
$btn-border-radius-sm: $border-radius;
// Forms
$input-padding-y: px-to-rem( 16px);
$input-padding-x: px-to-rem( 20px);
$input-bg: adjust-color( $gray-900, $hue: 0deg, $saturation: -2%, $lightness: 4%); // #2C2E30
$input-color: $gray-300;
$input-border-color: transparent;
$input-box-shadow: inset 2px 2px 8px rgba(#040405, 0.6);
$input-border-radius: $border-radius;
$input-focus-border-color: $input-border-color;
$input-focus-color: $input-color;
$input-focus-box-shadow: $input-box-shadow;
$input-placeholder-color: $body-color;
$form-group-margin-bottom: px-to-rem( 20px);
$custom-control-indicator-size: px-to-rem( 17px);
$custom-control-indicator-bg: #222426;
$custom-control-indicator-bg-size: 70% 70%;
$custom-control-indicator-border-color: transparent;
$custom-control-indicator-checked-color: $component-active-color;
$custom-control-indicator-checked-border-color: none;
$custom-checkbox-indicator-icon-checked: str-replace(url("data:image/svg+xml,%3Csvg width='11' height='8' viewBox='0 0 11 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.25 0.824219L3.25 7.82422L0.0507812 4.625L0.871094 3.80469L3.25 6.18359L9.42969 0.00390625L10.25 0.824219Z' fill='#{$custom-control-indicator-checked-color}'/%3E%3C/svg%3E%0A"),
"#",
"%23");
$custom-control-indicator-active-border-color: transparent;
$custom-checkbox-indicator-border-radius: px-to-rem( 3px);
// Z-index master list
$zindex-dropdown: 10;
$zindex-sticky: 11;
$zindex-popover: 12;
$zindex-tooltip: 13;
$zindex-fixed: 14;
$zindex-modal-backdrop: 15;
$zindex-modal: 16;
$zindex-loader: 17;
// Navs
$nav-link-padding-y: px-to-rem( 9px);
$nav-link-padding-x: px-to-rem( 40px);
$nav-tabs-link-active-color: null;
// Tooltips
$tooltip-color: $gray-900;
$tooltip-bg: $primary;
$tooltip-opacity: 1;
// Modals
$modal-inner-padding: px-to-rem( 50px);
$modal-content-border-radius: 0;
$modal-backdrop-bg: #27292B;
$modal-backdrop-opacity: .95;
$modal-xl: px-to-rem( 1140px);
$modal-lg: px-to-rem( 730px);
$modal-md: px-to-rem( 510px);